/*
================================================
GRID SYSTEM
================================================
*/

//
// @mixins
//

// For creating container, nested, and collapsed rows.
//
// 
// $behavior - Any special beavior for this row? Default: false. Options: nest, collapse, nest-collapse, false.
@mixin grid-row($behavior: false) {

  // use @include grid-row(nest); to include a nested row
  @if $behavior == nest {
    width: auto;
    @include unit(margin-#{$default-float}, -($column-gutter/2));
    @include unit(margin-#{$opposite-direction}, -($column-gutter/2));
    margin-top: 0;
    margin-bottom: 0;
    max-width: none;
  }

  // use @include grid-row(collapse); to collapsed a container row margins
  @else if $behavior == collapse {
    width: 100%;
    margin: 0;
    @include unit(max-width, $row-width);
  }

  // use @include grid-row(nest-collapse); to collapse outer margins on a nested row
  @else if $behavior == nest-collapse {
    width: auto;
    margin: 0;
    max-width: none;
  }

  // use @include grid-row; to use a container row
  @else {
    width: 100%;
    margin-#{$default-float}: auto;
    margin-#{$opposite-direction}: auto;
    margin-top: 0;
    margin-bottom: 0;
    @include unit(max-width, $row-width);
  }

  // Clearfix for all rows
  @include clearfix();
}

// Creates a column, should be used inside of a media query to control layouts
//
// $columns - The number of columns this should be
// $last-column - Is this the last column? Default: false.
// $center - Center these columns? Default: false.
// $offset - # of columns to offset. Default: false.
// $push - # of columns to push. Default: false.
// $pull - # of columns to pull. Default: false.
// $collapse - Get rid of gutter padding on column? Default: false.
// $float - Should this float? Default: true. Options: true, false, left, right.
@mixin grid-column(
  $columns:false, 
  $last-column:false, 
  $center:false, 
  $offset:false, 
  $push:false, 
  $pull:false, 
  $collapse:false, 
  $float:true) {

  position: relative;

  // If collapsed, get rid of gutter padding
  @if $collapse {
    padding-left: 0;
    padding-right: 0;
  }

  // Gutter padding whenever a column isn't set to collapse
  // (use $collapse:null to do nothing)
  @else if $collapse == false {
    @include unit(padding-left, $column-gutter / 2);
    @include unit(padding-right, $column-gutter / 2);
  }

  // If a column number is given, calculate width
  @if $columns {
    width: grid-calc($columns, $total-columns);

    // If last column, float naturally instead of to the right
    @if $last-column { float: $opposite-direction; }
  }

  // If offset, calculate appropriate margins
  @if $offset { margin-#{$default-float}: grid-calc($offset, $total-columns); }

  // Source Ordering, adds left/right depending on which you use.
  @if $push { #{$default-float}: grid-calc($push, $total-columns); #{$opposite-direction}: auto; }
  @if $pull { #{$opposite-direction}: grid-calc($pull, $total-columns); #{$default-float}: auto; }

  // If centered, get rid of float and add appropriate margins
  @if $center {
    margin-#{$default-float}: auto;
    margin-#{$opposite-direction}: auto;
    float: none !important;
  }

  @if $float {
    @if $float == left or $float == true { float: $default-float; }
    @else if $float == right { float: $opposite-direction; }
    @else { float: none; }
  }

}

// Create presentational classes for grid
//
// $size - Name of class to use, i.e. "large" will generate .large-1, .large-2, etc.
@mixin grid-html-classes($size) {

  @if $use-grid-push-pull == true {
    @for $i from 1 through $total-columns - 1 {
      .#{$size}-push#{-$i} {
        @include grid-column($push:$i, $collapse:null, $float:false);
      }
      .#{$size}-pull#{-$i} {
        @include grid-column($pull:$i, $collapse:null, $float:false);
      }
    }
  }

  .col { @include grid-column($columns:false); }


  @for $i from 1 through $total-columns {
    .#{$size}#{-$i} { @include grid-column($columns:$i,$collapse:null,$float:false); }
  }

  @for $i from 0 through $total-columns - 2 {
    .#{$size}-offset-#{$i} { @include grid-column($offset:$i, $collapse:null,$float:false); }
  }

  [class*="col"] + [class*="col"]:last-child { float: $opposite-direction; }
  [class*="col"] + [class*="col"].end { float: $default-float; }

  .col.#{$size}-centered { @include grid-column($center:true, $collapse:null, $float:false); }

  .col.#{$size}-uncentered {
    margin-#{$default-float}: 0;
    margin-#{$opposite-direction}: 0;
    float: $default-float !important;
  }

  .col.#{$size}-uncentered.opposite {
    float: $opposite-direction !important;

  }
}

@if $use-grids {
  .row {
    @include grid-row;

    &.collapse {
       > .col { @include grid-column($collapse:true); }

      .row {margin-left:0; margin-right:0;}
    }

    .row { @include grid-row($behavior:nest);
      &.collapse { @include grid-row($behavior:nest-collapse); }
    }
  }

  .col { @include grid-column($columns:$total-columns); }

  @include media-query($media:sm) {
    @include grid-html-classes($size:sm);
  }

  @include media-query($media:md) {
    @include grid-html-classes($size:md);
    // Old push and pull classes
    @if $use-grid-push-pull == true {
      @for $i from 1 through $total-columns - 1 {
        .push#{-$i} {
          @include grid-column($push:$i, $collapse:null, $float:false);
        }
        .pull#{-$i} {
          @include grid-column($pull:$i, $collapse:null, $float:false);
        }
      }
    }
  }
  @include media-query($media:lg) {
    @include grid-html-classes($size:lg);
  }
  @include media-query($media:xl) {
    @include grid-html-classes($size:xl);
  }
  @include media-query($media:xxl) {
    @include grid-html-classes($size:xxl);
  }
}


// Create a custom block grid
//
// $per-row - # of items to display per row. Default: false.
// $spacing - # of ems to use as padding on each block item. Default: rem-calc(20).
// $base-style - Apply a base style to block grid. Default: true.
@mixin block-grid(
  $per-row:false, 
  $spacing:$block-grid-default-spacing, 
  $base-style:true) {

  @if $base-style {
    display: block;
    padding: 0;
    //margin: 0 (-$spacing/2);
    @include unit(margin, 0 (-$spacing/2));
    @include clearfix;

    &>li {
      display: inline;
      height: auto;
      float: $default-float;
      //padding: 0 ($spacing/2) $spacing;
      @include unit(padding, 0 ($spacing/2) $spacing);
    }
  }

  @if $per-row {
    &>li {
      width: 100%/$per-row;
      //padding: 0 ($spacing/2) $spacing;
      @include unit(padding, 0 ($spacing/2) $spacing);

      &:nth-of-type(n) { clear: none; }
      &:nth-of-type(#{$per-row}n+1) { clear: both; }
    }
  }

}

// Generate presentational markup for block grid.
//
// $size - Name of class to use, i.e. "large" will generate .large-block-grid-1, .large-block-grid-2, etc.
@mixin compose-block-grid($size) {
  @for $i from 1 through $block-grid-elements {
    .#{$size}-block-grid-#{($i)} {
      @include block-grid($i,$block-grid-default-spacing,false);
    }
  }
}

@if $use-block-grids {
  [class*="block-grid-"] { @include block-grid; }
  
  /* === SMALL GRID CLASSES === */
  @media #{$sm-up} {
    @include compose-block-grid($size:sm);
  }

  /* === MEDIUM GRID CLASSES === */
  @media #{$md-up} {
    @include compose-block-grid($size:md);
  }

  /* === LARGE GRID CLASSES === */
  @media #{$lg-up} {
    @include compose-block-grid($size:lg);
  }
}
